import React from 'react'
import axios from 'axios'

export default function({setIsLoading,setImgURL}) {


    

    function upload(file) {

        const formData = new FormData()
        formData.append('image',file)
        // console.log(formData.get('filename'))
        console.log(formData.get('image'))
        axios.post('/uploads',formData)
        .then(res => {setIsLoading(false)
          setImgURL(res.data)
        })
        .catch(err => console.log(err))

        
    }
    const readFile = image => {
        const reader = new FileReader();
        console.log(image)
        reader.readAsDataURL(image);
        
        reader.onloadend = () => {
          console.log(reader.result)
          upload(reader.result);
        };
      };
    
      const handleChange= e => {
        setIsLoading(true);
        const image = e.target.files[0];
        // readFile(image);
        console.log(image)
        upload(image)
      };


    function onClickToUpLoad(e) {
        setIsLoading(true)
        console.log(e)
        const reader = new FileReader()
        const img = reader.readAsDataURL(e.target.files[0])
        console.log(img)
        reader.onloadend = () => {
            upload(img.result)

        }
    }


    return <div>
      <form encType='multipart/form-data'>
        <input type='file' id='upImage' name='upLoadImage' accept='image/*' onChange={handleChange}/>
        <label htmlFor='upImage'>Choose an Image</label>
      </form>
    </div>
}