import React, { useRef, useState } from 'react'

export default function({setIsLoading,imgURL}) {
    const [copyState,setCopyState] = useState('Click to copy')
    const textAreaRef = useRef('')
    const onClickToCopy = () => {
        textAreaRef.current.select()
        document.execCommand('copy')
        setCopyState('Copy success')
    }
    const onHover = () => {

        return <p>{copyState}</p>
    }
    return <>
    <form>
        <textarea type='text' ref={textAreaRef} value={imgURL} />
    </form>
        <button onClick={onClickToCopy} onMouseOver={onHover}>copy</button>
        <p>{copyState}</p>
    </>
} 